/* ////////////////////////////////////////

//  layout

//////////////////////////////////////// */
.is_container {
  width: 100vw;
  height: auto;
  margin: 0 auto;
  background: #FFFFFF;
}

.is_container .is_inner_container > h1 {
  display: none;
}

.is_section {
  max-width: 96rem;
  width: 100%;
  height: auto;
  margin: 0 auto;
  background: #FFFFFF;
}

@media screen and (max-width: 960px) {
  .is_section {
    max-width: 100%;
    width: calc(100% - 4rem);
  }
}

/* ////////////////////////////////////////

//  header

//////////////////////////////////////// */
.is_modal_bg {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: table;
  transition: all 0.5s cubic-bezier(0.25, 0.1, 0.25, 1);
  cursor: pointer;
}

.is_modal_close {
  position: fixed;
  top: 4rem;
  right: 4rem;
  display: inline-flex;
  width: 4em;
  height: 0.1em;
  background: #fff;
  border-radius: 0.1em;
  vertical-align: middle;
  transform: rotate(45deg);
  cursor: pointer;
}

.is_modal_close:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  transform: rotate(90deg);
}

@media screen and (max-width: 768px) {
  .is_modal_close {
    top: 2rem;
    right: 2rem;
    width: 2rem;
  }
}

.is_modal_container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.is_modal_wrapper {
  max-width: 43rem;
  width: 100%;
  height: auto;
  padding: 2rem;
  margin: 0 auto;
  background: #FFFFFF;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  text-align: center;
  transition: all 0.5s cubic-bezier(0.25, 0.1, 0.25, 1);
  position: relative;
}

@media screen and (max-width: 768px) {
  .is_modal_wrapper {
    width: calc(100% - 4rem);
  }
}

.is_modal_header .is_product_img {
  max-width: 35rem;
  width: 100%;
  height: auto;
  height: 35rem;
  margin: 0 auto 1.5rem;
  background: gray;
}

.is_modal_body .is_product_name {
  margin-bottom: 1rem;
  font-size: 2.4rem;
  font-weight: 600;
}

.is_modal_body .is_product_price {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.is_modal_body .is_product_description {
  font-size: 1.6rem;
}

.is_modal_body .is_product_link {
  margin: 1.5rem auto;
}

.is_modal_body .is_product_link a {
  display: inline-flex;
  padding: 1.5rem 3rem;
  background: gray;
  font-size: 1.6rem;
  font-weight: 600;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .is_modal_body .is_product_name {
    font-size: 2rem;
  }
  .is_modal_body .is_product_price {
    font-size: 1.6rem;
  }
  .is_modal_body .is_product_description {
    font-size: 1.4rem;
  }
  .is_modal_body .is_product_link a {
    font-size: 1.4rem;
  }
}

.is_modal_footer .is_modal_link ul {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
}

.is_modal_footer .is_modal_link ul li:first-of-type {
  margin-right: 2rem;
}

.is_modal_footer .is_modal_link p {
  font-size: 1.4rem;
}

.modal-enter {
  opacity: 0;
  transform: scale(1.1);
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .is_modal_wrapper,
.modal-leave-active .is_modal_wrapper {
  transform: scale(1.1);
}

