  font-size: 2.4rem;
  font-weight: 600;
}

.is_modal_body .is_product_price {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.is_modal_body .is_product_description {
  font-size: 1.6rem;
}

.is_modal_body .is_product_link {
  margin: 1.5rem auto;
}

.is_modal_body .is_product_link a {
  display: inline-flex;
  padding: 1.5rem 3rem;
  background: gray;
  font-size: 1.6rem;
  font-weight: 600;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .is_modal_body .is_product_name {
    font-size: 2rem;
  }
  .is_modal_body .is_product_price {
    font-size: 1.6rem;
  }
  .is_modal_body .is_product_description {
    font-size: 1.4rem;
  }
  .is_modal_body .is_product_link a {
    font-size: 1.4rem;
  }
}

.is_modal_footer .is_modal_link ul {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
}

.is_modal_footer .is_modal_link ul li:first-of-type {
  margin-right: 2rem;
}

.is_modal_footer .is_modal_link p {
  font-size: 1.4rem;
}

.modal-enter {
  opacity: 0;
  transform: scale(1.1);
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .is_modal_wrapper,
.modal-leave-active .is_modal_wrapper {
  transform: scale(1.1);
}
